<template>
  <div class="center" justify="center">
  
    <br />
     <v-btn block color="success">     BAR 102 ITE  </v-btn>
     <br />
     
    <v-app id="inspire">
      <v-row justify="center">
        <v-dialog v-model="dialog" persistent max-width="600px">
          <!-- Bouton create client -->
          <template v-slot:activator="{ on, attrs }">
            <v-btn color="primary" dark v-bind="attrs" v-on="on">
              Creation client
            </v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="text-h5">Client</span>
            </v-card-title>
            <v-card-text>
              <v-container>
                <infoclient></infoclient>
              </v-container>
              <small>*champs a remplir </small>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialog = false">
                Close
              </v-btn>
              <v-btn color="blue darken-1" text @click="dialog = false">
                Save
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-row>
      <br />
      <div>
        <v-stepper v-model="e13" vertical>
            <v-stepper-step step="1" :complete="e13 > 1">Client </v-stepper-step>
          <v-stepper-content step="1">
            <v-card color="grey lighten-4" class="mb-12" height="250px">
              <v-card-text>
                <v-container>
                  <infoclient></infoclient>
                </v-container>
                <small>*champs obligatoires</small>
              </v-card-text>
            </v-card>
            <v-btn color="primary" @click="e13 = 2"> Suivant </v-btn>
          </v-stepper-content>

          <v-stepper-step step="2" :complete="e13 > 2">Lieu de l'inspection  </v-stepper-step>
          <v-stepper-content step="2">
            <v-card color="grey lighten-4" class="mb-12" height="250px">
              <v-card-text>
                <v-container>
                  <infoinspection/>
                </v-container>
                <small>*champs obligatoires</small>
              </v-card-text>
            </v-card>
            <v-btn color="primary" @click="e13 = 3"> Suivant </v-btn>
            <v-btn text @click="e13 = 1"> Retour </v-btn>
          </v-stepper-content>

         <v-stepper-step step="3" :complete="e13 > 3">Bénéficiaire   </v-stepper-step>
          <v-stepper-content step="3">
            <v-card color="grey lighten-4" class="mb-12" height="250px">
              <v-card-text>
                <v-container> 
                  <infobeneficiaire/>                  
                </v-container>
                <small>*champs a remplir</small>
              </v-card-text>
            </v-card>
            <v-btn color="primary" @click="e13 = 4"> Suivant </v-btn>
            <v-btn text @click="e13 = 2"> Retour </v-btn>
          </v-stepper-content>

          <v-stepper-step step="4" :complete="e13 > 4">Entreprise  </v-stepper-step>
          <v-stepper-content step="4">
            <v-card color="grey lighten-4" class="mb-12" height="250px">
              <v-card-text>
                <v-container>
                  <infoentreprise/>
                </v-container>
                <small>*champs a remplir</small>
              </v-card-text>
            </v-card>
            <v-btn color="primary" @click="e13 = 5"> Suivant </v-btn>
            <v-btn text @click="e13 = 3"> Retour </v-btn>
          </v-stepper-content>

          <v-stepper-step :rules="[() => false]" step="5" :complete="e13 > 5">           Nature des travaux           <small>Alert message</small>
          </v-stepper-step>
          <v-stepper-content step="5">
            <v-card color="grey lighten-4" class="mb-12" height="800px">
              <v-card-text>
                <v-container>
                    <v-radio-group       v-model="row"     row    label="Etat des travaux lors de la vérification" dense >
                        <v-radio       label="Murs brut"             value="radio-1"       ></v-radio>
                        <v-radio       label="Panneau d'isolation posés"    value="radio-2"       ></v-radio>
                        <v-radio       label="Enduis de base ou sous-enduit appliqué"    value="radio-3"    ></v-radio>
                        <v-radio       label="Revetement de finition"        value="radio-4"        ></v-radio>
                    </v-radio-group>

                    <v-radio-group     v-model="row"     row    label="Type de chauffage utilisé"   dense >
                        <v-radio       label="Electricité"             value="radio-1"       ></v-radio>
                        <v-radio       label="Combustible"    value="radio-2"       ></v-radio>
                        <v-radio       label="Mixte"    value="radio-3"    ></v-radio>
                        </v-radio-group>

                     <v-radio-group    v-model="row"     row    label="Type de l'isolant utilisé"   dense >
                        <v-radio       label="PSE"             value="radio-1"       ></v-radio>
                        <v-radio       label="Laine de roche"    value="radio-2"       ></v-radio>
                        <v-radio       label="Laine de verre"    value="radio-3"    ></v-radio>
                        <v-radio       label="Fibre de bois"        value="radio-4"        ></v-radio>                        
                        <v-radio       label="Non précisé"        value="radio-5"        ></v-radio>
                        <v-radio       label="Autre"        value="radio-6"        ></v-radio>
                        <v-radio       label="Non communiqué"        value="radio-7"        ></v-radio>
                      </v-radio-group>
                
                     
                        <v-combobox    v-model="selectFixation"    :items="itemsFixation"     label="Mode de fixation "          multiple  dense 
                        chips
                        ></v-combobox>
                
                   
                     <v-radio-group       v-model="row"     row    label="Type de support"   dense >
                        <v-radio       label="Béton"             value="radio-1"       ></v-radio>
                        <v-radio       label="Terre cuite"    value="radio-2"       ></v-radio>
                        <v-radio       label="Béton cellulaire"    value="radio-3"    ></v-radio>
                        <v-radio       label="Moellons"        value="radio-4"        ></v-radio>                        
                        <v-radio       label="Béton banché"        value="radio-5"        ></v-radio>
                        <v-radio       label="Autre"        value="radio-6"        ></v-radio>
                        <v-radio       label="Communiqué"        value="radio-7"        ></v-radio>
                      </v-radio-group>
                
                     <v-switch   v-model="switch1"
                        :label="`L'isolation est posée entre un volume chauffé et un volume non chauffé : ${switch1.toString()}`"
                    ></v-switch>
                
                     <v-switch   v-model="switch2"
                        :label="`Le bénéficiaire a déclaré ne jamais avoir eu en sa possession de facture, devis et l'atteste par écrit : ${switch2.toString()}`"
                    ></v-switch>

                    <!--Date ici -->
                     
                    <v-row>
                          <v-col  cols="12"  lg="6">
                                <v-switch   v-model="switch3" label="Devis présenté pour preuve de réalisation des travaux" dense/>
                            </v-col>
                          <v-col  cols="12"  lg="6">
                               <v-text-field  label="Référence" hint="Référence du devis" dense></v-text-field>
                            </v-col>
                            
                    </v-row>
                    <v-row>
                          <v-col  cols="12"  lg="6">
                                <v-switch   v-model="switch4" label="Facture présentée pour preuve de réalisation des travaux" dense/>
                            </v-col>
                          <v-col  cols="12"  lg="6">
                               <v-text-field  label="Référence" hint="Référence de la facture" dense ></v-text-field>
                            </v-col>
                            
                    </v-row>

                     <v-row>
                          <v-col  cols="12"  lg="6">
                                <v-switch   v-model="switch5" label="Présentation du cadre de contribuation" dense/>
                            </v-col>
                          <v-col  cols="12"  lg="6">
                               <v-text-field  label="Référence" hint="Référence du cadre de contribuation" dense ></v-text-field>
                            </v-col>                            
                    </v-row>

                     <v-row>
                          <v-col  cols="12"  lg="6">
                                <v-switch   v-model="switch6" label="Installation de plus de 2 ans" dense/>
                            </v-col>
                          <v-col  cols="12"  lg="6">
                               <v-text-field  label="Année de construction "  dense ></v-text-field>
                            </v-col>                            
                    </v-row>
                     
                </v-container>
              </v-card-text>
            </v-card>
            <v-btn color="primary" @click="e13 = 6"> Suivant </v-btn>
            <v-btn text @click="e13 = 4"> Retour </v-btn>
          </v-stepper-content>

          <v-stepper-step step="6"> Inspection </v-stepper-step>
          <v-stepper-content step="6">
            <v-card
              color="grey lighten-4"
              class="mb-12"
              height="800px"
            >
            <v-card-text>
                <v-container>
                  <v-tabs fixed-tabs  dark  show-arrows 
                    background-color="green" 
                    ><!-- background-color="teal darken-3" -->
                    <v-tab>Résultat de la vérification</v-tab>
                    <v-tab>Surface</v-tab>
                    <v-tab>Condition de mise en oeuvre</v-tab>                    
                    <v-tab>Qualité des travaux</v-tab>                                   
                    <v-tab>Points singuliers</v-tab>
                  
                   <v-tab-item>
                    <v-card flat>
                      <v-card-text>
                        <v-radio-group       v-model="row"     row    label="Etat des travaux lors de la vérification" dense >
                        <v-radio       label="Satifaisant"             value="radio-1"       ></v-radio>
                        <v-radio       label="Non Satisfaisant"    value="radio-2"       ></v-radio>
                        <v-radio       label="Non Vérifiable "    value="radio-3"    ></v-radio>
                       </v-radio-group>
                        <v-select
                            v-model="e7"
                            :items="states"
                            label="Observation"
                            multiple
                            chips
                            hint="Sélectionner la ou les raison(s)"
                            persistent-hint
                            ></v-select>
                      </v-card-text>
                    </v-card>
                   </v-tab-item>

                   <v-tab-item>
                      <v-row>
                       <v-col cols="12" sm="6">
                          <v-text-field
                          label="Surface m2 réalisé*"
                          type="surfacerealise"
                          required
                          dense
                          ></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6">
                          <v-text-field
                          label="Surface m2 mesurée*"
                          type="surfacemesuree"
                          required
                          dense
                          ></v-text-field>
                      </v-col>
                     </v-row>
                     <h2> Add Tab here </h2>
                     <v-select
                        v-model="e7"
                        :items="observSurface"
                        label="Observation"
                        multiple
                        chips
                        hint="Sélectionner la ou les raison(s)"
                        persistent-hint
                        ></v-select>
                  </v-tab-item>
                  </v-tabs> 

                    <v-btn block color="success"> 2. Résultat de la vérification  </v-btn>
                     <v-radio-group       v-model="row"     row    label="Etat des travaux lors de la vérification" dense >
                        <v-radio       label="Satifaisant"             value="radio-1"       ></v-radio>
                        <v-radio       label="Non Satisfaisant"    value="radio-2"       ></v-radio>
                        <v-radio       label="Non Vérifiable "    value="radio-3"    ></v-radio>
                       </v-radio-group>
                    <v-select
                        v-model="e7"
                        :items="states"
                        label="Observation"
                        multiple
                        chips
                        hint="Sélectionner la ou les raison(s)"
                        persistent-hint
                        ></v-select>
                   <br>
                    <v-btn block color="success"> 3. Surface   </v-btn>
                    <br>
                     <v-row>
                       <v-col cols="12" sm="6">
                          <v-text-field
                          label="Surface m2 réalisé*"
                          type="surfacerealise"
                          required
                          dense
                          ></v-text-field>
                      </v-col>
                      <v-col cols="12" sm="6">
                          <v-text-field
                          label="Surface m2 mesurée*"
                          type="surfacemesuree"
                          required
                          dense
                          ></v-text-field>
                      </v-col>
                     </v-row>
                     <h2> Add Tab here </h2>
                     <v-select
                        v-model="e7"
                        :items="observSurface"
                        label="Observation"
                        multiple
                        chips
                        hint="Sélectionner la ou les raison(s)"
                        persistent-hint
                        ></v-select>

                </v-container>
            </v-card-text>
            
            
            </v-card>
            <v-btn color="primary" @click="e13 = 7"> Suivant </v-btn>
            <v-btn text @click="e13 = 5"> Retour </v-btn>
          </v-stepper-content>

           <v-stepper-step step="7"> Photos </v-stepper-step>
          <v-stepper-content step="7">
            <v-card
              color="grey lighten-4"
              class="mb-12"
              height="300px">
             <v-card-text>
                <v-container>
                  <uploadimage></uploadimage>

                </v-container>
             </v-card-text>
            
            </v-card>
            <v-btn color="primary" @click="e13 = 8"> Suivant </v-btn>
             <v-btn text @click="e13 = 6"> Retour </v-btn>
          </v-stepper-content>

           <v-stepper-step step="8"> Signature </v-stepper-step>
          <v-stepper-content step="8">
            <v-card
              color="grey lighten-1"
              class="mb-12"
              height="200px"
            ></v-card>
            <v-btn color="primary" @click="e13 = 9"> Suivant </v-btn>
             <v-btn text @click="e13 = 7"> Retour </v-btn>
          </v-stepper-content>

        </v-stepper>
      </div>

     
    </v-app>
  </div>
</template>



<script>
import infoclient from '../components/form/InfoClient'
import infoinspection from '../components/form/InfoInspection'
import infobeneficiaire from '../components/form/InfoBeneficiaire'
import infoentreprise from '../components/form/InfoEntreprise';
import uploadimage from '../components/form/UploadDropImage';


export default {
  name: "BAR102",

  components: {
    infoclient,
    infoinspection,
    infobeneficiaire,
    infoentreprise,
    uploadimage
    },
    
  data() {
    return {
      dialog: false,
      e13: 1,
      formData: {
        client: {
          adresse: {},
        },
      },
      page:1,
        switch1: false,
        switch2: false,
        switch3: false,
        switch4: false,  
        switch5: false,
        switch6: false,

    
        e7: [],
        states: [
          'Travaux disolation non réalisés', 
          'Installation inacessible de par son aménagement',
          'Incident de visite - accès refusé par le propriétaire des lieux',
          'Absence de documentation permettant de se prononcer sur la nature des matériaux installés',
        ],
        observSurface        : [
          'Le type, la nature de l isolant n est de la même nature que celui déclaré par le professionnel en charge des travaux.', 
          'Le type, la nature de l isolant mis en œuvre par le professionnel en charge de la réalisation des travaux n est adapté à l opération d isolation pris en référence dans la preuve de réalisation. (cf. fiche technique fabricant, certificat produit de la construction).',
          'La surface mesurée ne correspond pas à celle déclarée par le professionnel ayant réalisé les travaux.', 
        ],
      selectFixation: ['Collée', 'Collée chevillée'],
        itemsFixation: [
          'Collée',
          'Collée chevillée',
          'Mécanique',
          'Non précisé',
          'Autre',
          'Non communiqué'],
      
      //date format
      
    };
  },
};
</script>