            <v-divider></v-divider>
            <v-stepper-step :complete="e1 > 5" step="5" >Nature des travaux </v-stepper-step>
            <v-divider></v-divider>
            <v-stepper-step :complete="e1 > 6" step="6" >Inspection</v-stepper-step>
