                <v-container>
                  <infoclient></infoclient>
                </v-container>
                <small>*champs obligatoires</small>
              </v-card-text>
            </v-card>
            <v-btn color="primary" @click="e1 = 2"> Suivant </v-btn>
          </v-stepper-content>

          <v-stepper-content step="2">
            <v-card color="grey lighten-3" class="mb-12" height="300px">
              <v-card-text>
                <v-container>
                  <infoinspection/>
                </v-container>
                <small>*champs obligatoires</small>
              </v-card-text>
            </v-card>
            <v-btn color="primary" @click="e1 = 3"> Suivant </v-btn>
            <v-btn text @click="e1 = 1"> Retour </v-btn>
          </v-stepper-content>

           <v-stepper-content step="3">
