                          <v-switch  dense v-model="switch2" :label="`Pièces remises au bénéficiaire mais non présentées	 `"></v-switch>
                          <v-switch  dense v-model="switch3" :label="`Pièces non remises au bénéficiaire avec déclaration écrite de celui-ci `"></v-switch>
