                          </v-radio-group>
                          <v-radio-group       v-model="row"     row    label="Capot de protection" dense >
                            <v-radio       label="Présent"             value="radio-10"       ></v-radio>
                            <v-radio       label="Manquant"    value="radio-20"       ></v-radio>
                            <v-radio       label="Non vérifiable "    value="radio-30"    ></v-radio>                    
                            <v-radio       label="Non nécessaire "    value="radio-40"    ></v-radio>
                          </v-radio-group>
                          <v-radio-group       v-model="row"     row    label="Ecart au feu" dense >
                            <v-radio       label="Présent"             value="radio-11"       ></v-radio>
                            <v-radio       label="Manquant"       value="radio-21"       ></v-radio>
                            <v-radio       label="Non vérifiable "    value="radio-31"    ></v-radio>                    
                            <v-radio       label="Non nécessaire "    value="radio-41"    ></v-radio>
                          </v-radio-group>
                          <v-radio-group       v-model="row"     row    label="Réhausse de trappe" dense >
                            <v-radio       label="Présent"             value="radio-12"       ></v-radio>
                            <v-radio       label="Manquant"    value="radio-22"       ></v-radio>
                            <v-radio       label="Non vérifiable "    value="radio-32"    ></v-radio>                    
                            <v-radio       label="Non nécessaire "    value="radio-42"     ></v-radio>
                          </v-radio-group>
                          <v-radio-group       v-model="row"     row    label="Trappe bloquée" dense >
                            <v-radio       label="Présent"             value="radio-13"       ></v-radio>
                            <v-radio       label="Manquant"    value="radio-23"       ></v-radio>
                            <v-radio       label="Non vérifiable "    value="radio-33"    ></v-radio>                    
                            <v-radio       label="Non nécessaire "    value="radio-43"     ></v-radio>
                          </v-radio-group>
                          <v-radio-group       v-model="row"     row    label="Pige ou repérage de hauteur" dense >
                            <v-radio       label="Présent"             value="radio-14"       ></v-radio>
                            <v-radio       label="Manquant"    value="radio-24"       ></v-radio>
                            <v-radio       label="Non vérifiable "    value="radio-34"    ></v-radio>                    
                            <v-radio       label="Non nécessaire "    value="radio-44"   ></v-radio>
                          </v-radio-group>
                           <v-radio-group       v-model="row"     row    label="Trace d’humidité" dense >
                            <v-radio       label="Présent"             value="radio-15"       ></v-radio>
                            <v-radio       label="Manquant"    value="radio-25"       ></v-radio>
                            <v-radio       label="Non vérifiable "    value="radio-35"    ></v-radio>                    
                            <v-radio       label="Non nécessaire "    value="radio-45"     ></v-radio>
                          </v-radio-group>
                          <v-select
                            v-model="e7"
                            :items="observQualiteTravaux"
                            label="Commentaires / Criteres de non qualité manifeste"
