                        <v-radio       label="Non Satisfaisant"    value="radio-2"       ></v-radio>
                        <v-radio       label="Non Vérifiable "    value="radio-3"    ></v-radio>
                       </v-radio-group>
                        <v-select
                            v-model="e7"
                            :items="observConclusion"
                            label="Observation"
                            multiple
                            chips
                            hint="Sélectionner la ou les raison(s)"
                            persistent-hint
                            ></v-select>
                          
                        <v-select
                            v-model="e7"
                            :items="observQualiteRegleDeLArt"
                            label="Commentaires / Criteres de non qualité manifeste"
                            multiple
                            chips
                            hint="Sélectionner la ou les raison(s)"
                            persistent-hint
                            ></v-select>
                            
