<script>
import infoclient from '../components/form/InfoClient'
import infoinspection from '../components/form/InfoInspection'
import infobeneficiaire from '../components/form/InfoBeneficiaire'
import infoentreprise from '../components/form/InfoEntreprise';
import uploadimage from '../components/form/UploadDropImage';


export default {
  name: "BAR101",

  components: {
    infoclient,
    infoinspection,
    infobeneficiaire,
    infoentreprise,
    uploadimage
    },
    
  data() {
    return {
      dialog: false,
      e1: 1,
      e13: 1,

      //faire avec v-bind
      formData: {
        client: {
          adresse: {},
        },
      },
      row:null,
      page:1,
        switch1: false,
        switch2: false,
        switch3: false,
        switch4: false,  
        switch5: false,
        switch6: false,
        switch7: false,
        switch8: false,

        radio_group:null,
        e7: [],
        observConclusion: [
          'Isolation réalisée par le toit du bâtiment sans accès possible par une trappe', 
          'Absence de moyen d’accès ou moyen d’accès non sécurisé au lieu de l’opération d’isolation',
        ],
       
       
        observIsolant:[
